<template>
  <div class="pb-10 mx-2 mt-10 md:w-1/2 md:mx-auto poppins">
    <vee-form
      :validation-schema="schema"
      @submit="register"
      :initial-values="userData"
    >
      <template v-for="(f, i) in fields" :key="i">
        <div class="mb-3">
          <label class="inline-block mb-2 dark:text-white">{{ f.label }}</label>
          <vee-field
            type="text"
            :name="f.name"
            class="form-control"
            :placeholder="f.placeholder"
          />
          <ErrorMessage class="text-red-600" :name="f.name" />
        </div>
      </template>
      <!-- HSC Batch -->
      <div class="mb-3">
        <label class="inline-block mb-2 dark:text-white">HSC Batch</label>
        <vee-field as="select" name="hsc_batch" class="form-control">
          <option value="" disabled selected>Select Your Batch</option>
          <option value="2021">2021</option>
          <option value="2022">2022</option>
          <option value="2023">2023</option>
          <option value="others">Others</option>
        </vee-field>
        <ErrorMessage class="text-red-600" name="hsc_batch" />
      </div>
      <div class="mb-3">
        <label class="inline-block mb-2 dark:text-white">SSC Board</label>
        <vee-field
          as="select"
          name="ssc_board"
          class="
            block
            w-full
            py-1.5
            px-3
            text-gray-800
            border border-gray-300
            transition
            duration-500
            focus:outline-none focus:border-black
            rounded
          "
          placeholder="Board"
        >
          <option value="" disabled selected>Select Board</option>
          <option value="barisal">Barisal</option>
          <option value="chittagong">Chittagong</option>
          <option value="comilla">Comilla</option>
          <option value="dhaka">Dhaka</option>
          <option value="dinajpur">Dinajpur</option>
          <option value="jessore">Jessore</option>
          <option value="madrasah">Madrasah</option>
          <option value="rajshahi">Rajshahi</option>
          <option value="sylhet">Sylhet</option>
          <option value="mymensingh">Mymensingh</option>
          <option value="tec">Technical</option>
        </vee-field>
        <ErrorMessage class="dark:text-red-500 text-red-600" name="ssc_board" />
      </div>
      <!-- Email -->
      <div class="mb-3">
        <label class="inline-block mb-2 dark:text-white">Email</label>
        <vee-field
          type="email"
          name="email"
          class="form-control"
          placeholder="Enter Email"
        />
        <ErrorMessage class="text-red-600" name="email" />
      </div>

      <!-- Password -->
      <div class="mb-3">
        <label class="inline-block mb-2 dark:text-white">Password</label>
        <vee-field name="password" :bails="false" v-slot="{ field, errors }">
          <input
            type="password"
            class="form-control"
            placeholder="Password"
            v-bind="field"
          />
          <div class="text-red-600" v-for="(error, i) in errors" :key="i">
            {{ error }}
          </div>
        </vee-field>
        <!-- <ErrorMessage class="text-red-600" name="password" /> -->
      </div>
      <!-- Confirm Password -->
      <div class="mb-3">
        <label class="inline-block mb-2 dark:text-white"
          >Confirm Password</label
        >
        <vee-field
          type="password"
          name="confirm_password"
          class="form-control"
          placeholder="Confirm Password"
        />
        <ErrorMessage class="text-red-600" name="confirm_password" />
      </div>
      <div
        :class="reg_alert_variant"
        class="p-5 my-4 font-bold text-center text-white"
        v-if="reg_show_alert"
      >
        {{ reg_alert_msg }}
      </div>
      <button
        type="submit"
        class="
          block
          w-full
          bg-red-500
          text-white
          py-1.5
          px-3
          rounded
          transition
          hover:bg-red-600
        "
        :disabled="reg_in_submission"
      >
        Create an account
      </button>
    </vee-form>
  </div>
</template>

<script>
export default {
  name: "RegisterFrom",

  data() {
    return {
      schema: {
        name: "required|min:3|max:100",
        email: "required|min:3|max:100|email",
        fathersName: "required|min:3|max:100",
        mothersName: "required|min:3|max:100",
        college: "required|min:3|max:200",
        hsc_batch: "required",
        school: "required|min:3|max:200",
        ssc_roll: "required|min:3|max:20",
        ssc_registration: "required|min:3|max:20",
        ssc_board: "required",
        facebook_link: "required|url",
        password: "required|min:3|max:100",
        confirm_password: "password_mismatch:@password",
      },

      fields: [
        { name: "name", label: "Name", placeholder: "Enter your name" },
        {
          name: "fathersName",
          label: "Father's Name",
          placeholder: "Father's Name",
        },
        {
          name: "mothersName",
          label: "Mother's Name",
          placeholder: "Mothers Name",
        },

        { name: "college", label: "College", placeholder: "College" },
        { name: "school", label: "School", placeholder: "School you studied" },
        { name: "ssc_roll", label: "SSC Roll", placeholder: "SSC Roll" },
        {
          name: "ssc_registration",
          label: "SSC Registration",
          placeholder: "SSC Registration",
        },
        {
          name: "facebook_link",
          label: "Facebook ID Link",
          placeholder: "https://facebook.com/xyz...",
        },
      ],
      reg_in_submission: false,
      reg_show_alert: false,
      reg_alert_variant: "bg-blue-500",
      reg_alert_msg:
        "Please wait! Untill your registration has been completed.",
      userData: {},
    };
  },
  methods: {
    async register(values) {
      this.reg_show_alert = true;
      this.reg_in_submission = true;
      this.reg_alert_variant = "bg-blue-500";
      this.reg_alert_msg =
        "Please wait! Untill your registration has been completed";

      try {
        await this.$store.dispatch("register", {
          values,
          router: this.$router,
        });
      } catch (err) {
        console.log(err);
        this.reg_in_submission = false;
        this.reg_alert_variant = "bg-red-500";
        this.reg_alert_msg =
          "An Unexpected error occurred. Please try again later.";
        return;
      }

      this.reg_alert_variant = "bg-green-500";
      this.reg_alert_msg = "Success! Your account has been registered.";
    },
  },
};
</script>

<style lang="scss" scoped>
</style>
